import React, {Component} from 'react';
import classnames from "classnames";
import {Button, Popconfirm, Spin, Icon} from 'antd';
import Number from "./Number";

const noop = () => {
};

class StockItem extends Component {

    order = async () => {
        this.setState({loading: true});
        try {
            await this.props.onOrder(this.state.orderCount);
        } catch {
        }
        this.setState({orderCount: 0, loading: false});
    };

    constructor(props, context) {
        super(props, context);
        this.state = {
            orderCount: 0,
            loading: false,
        };
    }

    render() {
        return <div className={
            classnames({
                "stock-item": true,
                "enlarged": this.props.enlarged,
                "hidden": this.props.hidden,
            })
        }>
            <div className="main">
                <div className="name" onClick={this.props.enlarged ? noop : this.props.onEnlarge}>
                    <span className={"return-icon"} onClick={this.props.enlarged ? this.props.onShrink : noop}>
                        <Icon type="arrow-left"/>
                    </span> {this.props.name}
                </div>
                <div className="price">
                    单价: ¥{(+this.props.price).toFixed(2)}
                </div>
                {this.props.loading ? <div className="purchased">
                        &nbsp;
                        <Spin size="small"/>
                    </div> :
                    <div className="purchased">
                        已购: {this.props.ownedCount || 0} 件
                        {this.props.stockCount >= 0 &&
                        <span className="stock-count">库存{this.props.stockCount || 0}件</span>}
                    </div>}
            </div>
            <div className="side">
                <Number value={this.state.orderCount}
                        onChange={orderCount => this.setState({orderCount})}
                        disabled={this.state.loading}
                />
                <Popconfirm
                    placement={"left"}
                    title={`确定要订购${this.state.orderCount}件 ${this.props.name} 吗？ (总价${(this.state.orderCount * this.props.price).toFixed(2)}元)`}
                    className={!this.state.orderCount ? "disabled" : ""}
                    onConfirm={this.order}
                >
                    <Button
                        className="purchase-button"
                        type={"primary"}
                        disabled={!this.state.orderCount}
                        loading={this.state.loading}>订购</Button>
                </Popconfirm>
            </div>

            <div className={classnames({"hidden": !this.props.enlarged, "item-description": true})}>
                {!!this.props.description ?
                    <div dangerouslySetInnerHTML={{__html: this.props.description}}/>
                    :
                    <div style={{opacity: 0.7}}>
                        商品暂无详细说明
                    </div>}
            </div>
        </div>
    }
}

export default StockItem;