import React, {Component} from 'react';

class Balance extends Component {

    updateCurrent = () => {
        if (this.state.current === this.props.balance) {
            return;
        }
        if (Math.abs(this.state.current - this.props.balance) < 0.01) {
            this.setState({
                current: this.props.balance
            })
        } else {
            this.setState({
                current: this.state.current + (this.props.balance - this.state.current) / 10
            })
        }
    };

    constructor(props, context) {
        super(props, context);
        this.state = {current: this.props.balance};

        setInterval(this.updateCurrent, 10);
    }

    render() {
        return <div className="balance-component">
            可用余额 {this.props.balance === null ? <span>...</span> :
            <span>¥ {(+this.state.current).toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}</span>}
        </div>
    }

}

export default Balance;