import axios from 'axios';
import {message} from "antd";

window.backendUrl = window.backendUrl || "/";

function assertResult(result) {
    if (result.success === false) {
        throw new Error("result.success===false");
    }
}

function getBackendUrl(path) {
    if (window.backendUrl === '/') {
        window.backendUrl = window.location.origin;
    }
    if (window.backendUrl.endsWith("/")) {
        window.backendUrl = window.backendUrl.substr(0, window.backendUrl.length - 1);
    }

    if (!path.startsWith("/")) {
        path = "/" + path;
    }

    return window.backendUrl + path;
}

export default {
    async createUser(userName) {
        try {
            const result = (await axios.post(getBackendUrl("/createUser"), {userName})).data;
            assertResult(result);
        } catch (e) {
            message.error("Failed to create user: " + e.message);
            throw e;
        }
    },
    async query(userName) {
        try {
            const result = (await axios.post(getBackendUrl("/query"), {userName})).data;
            assertResult(result);
            return result;
        } catch (e) {
            message.error("Failed to query stock: " + e.message);
            throw e;
        }
    },
    async querySorted(userName) {
        try {
            const result = (await axios.post(getBackendUrl("/querySorted"), {userName})).data;
            assertResult(result);
            return result;
        } catch (e) {
            message.error("Failed to query stock: " + e.message);
            throw e;
        }
    },
    async queryBalance(userName) {
        try {
            const result = (await axios.post(getBackendUrl("/queryBalance"), {userName})).data;
            assertResult(result);
            return result;
        } catch (e) {
            message.error("Failed to query balance: " + e.message);
            throw e;
        }
    },
    async purchase(userName, productCode, count) {
        try {
            const result = (await axios.post(getBackendUrl("/purchase"), {userName, productCode, count})).data;
            assertResult(result);
            return result;
        } catch (e) {
            message.error("Failed to purchase: " + e.message);
            throw e;
        }
    },
}