import React, {Component} from 'react';
import {Button, Icon, Input} from 'antd';

const normalizeVal = val => Math.min(99, Math.max(0, val));

function isInteger(value) {
    return !isNaN(value) && parseInt(value) === +value;
}

class Number extends Component {
    onChange = e => {
        if (!e.target.value) {
            this.props.onChange(0);
            return;
        }
        if (!isInteger(e.target.value)) {
            return;
        }
        this.props.onChange(normalizeVal(+e.target.value));
    };

    increment = (delta) => {
        let val = this.props.value + delta;
        if (isNaN(val)) {
            val = 0;
        }
        this.props.onChange(normalizeVal(val));
    };

    render() {
        return <div className="number-component">
            <Button disabled={this.props.disabled} shape="circle" onClick={() => this.increment(-1)}><Icon
                type="minus"/></Button>
            <span>
                <Input disabled={this.props.disabled} className="input" value={this.props.value}
                       onChange={this.onChange}/>
            </span>
            <Button disabled={this.props.disabled} shape="circle" onClick={() => this.increment(1)}><Icon type="plus"/></Button>
        </div>
    }

}

export default Number;