import React, {Component} from 'react';
import {Breadcrumb, Layout, message, Spin} from 'antd';
import StockItem from "./StockItem";
import Balance from "./Balance";
import UserName from "./UserName";
import Service from './Service'
import common from "./common";

const {Header, Content, Footer} = Layout;

class App extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {balance: null, stockItems: null, enlargedProductCode: null};
    }

    resizeHandler = () => {
        if (this.recalcEnlargedElementStyle) {
            this.recalcEnlargedElementStyle();
        }
    };

    async componentDidMount() {
        await common.initUser();
        this.refreshBalance();
        await this.refreshStockItems();

        window.addEventListener("resize", this.resizeHandler);
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.resizeHandler);

    }

    refreshStockItems = async () => {
        let items = await Service.query(localStorage["userName"]);
        items.forEach(item => item.loading = false);
        this.setState({stockItems: items});
    };

    refreshBalance = async () => {
        let result = await Service.queryBalance(localStorage["userName"]);
        this.setState({balance: result.balance});
    };

    onOrder = async (item, count) => {
        await Service.purchase(localStorage["userName"], item.productCode, count);
        message.success("恭喜，订购成功");
        this.setState({balance: this.state.balance - item.price * count});
        item.loading = true;
        this.setState({});
        this.refreshStockItems();
        this.refreshBalance();
    };

    render() {
        return (
            <Layout className="layout">
                <Header>
                    <div className="logo">KubeCon DEMO</div>
                </Header>
                <Content style={{padding: '0 50px'}}>
                    <Breadcrumb style={{margin: '16px 0'}}>
                        <Breadcrumb.Item>Home</Breadcrumb.Item>
                        <Breadcrumb.Item>库存管理</Breadcrumb.Item>
                    </Breadcrumb>
                    <UserName/>
                    <div style={{background: '#fff', padding: 24, minHeight: 280}} id={"stock-item-list"}>
                        {!this.state.stockItems && <div className="spin-div"><Spin/></div>}
                        {!!this.state.stockItems
                        && this.state.stockItems.map(item =>
                            <StockItem key={item.productCode}
                                       name={item.name}
                                       description={item.description}
                                       ownedCount={item.ownedCount}
                                       stockCount={item.stockCount}
                                       price={item.price}
                                       loading={item.loading}
                                       enlarged={this.state.enlargedProductCode === item.productCode}
                                       hidden={!!this.state.enlargedProductCode && this.state.enlargedProductCode !== item.productCode}
                                       onEnlarge={() => this.onEnlarge(item.productCode)}
                                       onShrink={() => this.onShrink()}
                                       onOrder={(count) => this.onOrder(item, count)}/>)}
                    </div>
                </Content>

                <Balance balance={this.state.balance}/>
                <Footer style={{textAlign: 'center'}}>{common.footerText}</Footer>
            </Layout>
        );
    }

    onEnlarge(productCode) {
        this.setState({enlargedProductCode: productCode}, () => {
            const CONTAINER_PADDING = 24;
            const FOOTER_HEIGHT = 105;
            const TRANSITION_DURATION = 350;
            const list = document.getElementById("stock-item-list");
            const listRect = list.getBoundingClientRect();
            let enlargeIndex = -1;
            const styleToApply = {};
            for (let i = 0; i < list.children.length; i++) {
                let rect = list.children[i].getBoundingClientRect();
                if (list.children[i].className.indexOf("enlarged") >= 0) {
                    enlargeIndex = i;
                    let marginTop = rect.y - listRect.y - CONTAINER_PADDING;
                    let marginBottom = listRect.y + listRect.height - rect.y - rect.height - CONTAINER_PADDING;
                    styleToApply[i] = `margin-top:${marginTop}px;margin-bottom:${marginBottom}px;height:${rect.height}px;`;
                } else if (list.children[i].className.indexOf("hidden") >= 0) {
                    styleToApply[i] = `position:absolute; width:${rect.width}px; top:${rect.y}px`;
                }
            }

            Object.keys(styleToApply).forEach(key => {
                list.children[key].style = styleToApply[key];
            });

            if (enlargeIndex >= 0) {
                this.recalcEnlargedElementStyle = () => {
                    const TRANSITION_DURATION_TEXT = (TRANSITION_DURATION / 1000).toFixed(2) + "s";
                    let height = document.body.clientHeight - listRect.y - CONTAINER_PADDING * 2 - FOOTER_HEIGHT;
                    list.children[enlargeIndex].style = `transition: opacity ${TRANSITION_DURATION_TEXT}, margin-top ${TRANSITION_DURATION_TEXT}, margin-bottom ${TRANSITION_DURATION_TEXT}, height ${TRANSITION_DURATION_TEXT};margin-top:0; margin-bottom:0; height:${height}px`;
                };
                setTimeout(this.recalcEnlargedElementStyle, TRANSITION_DURATION / 3);
            }
        });
    }

    onShrink() {
        this.recalcEnlargedElementStyle = null;
        const list = document.getElementById("stock-item-list");
        for (let i = 0; i < list.children.length; i++) {
            list.children[i].style = "";
        }
        this.setState({enlargedProductCode: null});
    }
}

export default App;