import React, {Component} from 'react';
import {Icon, Popconfirm, Tooltip} from "antd";

class UserName extends Component {

    constructor(props, context) {
        super(props, context);
        this.state = {
            userName: localStorage['userName']
        };
        setInterval(() => {
            this.setState({userName: localStorage['userName']})
        }, 1000);
    }

    switchUser = () => {
        delete localStorage["userName"];
        window.location.reload();
    };

    render() {
        if (!this.state.userName) {
            return null;
        }
        return <div className="username-display">
            <Icon type="user"/>
            <Tooltip title={"用户标识符: " + this.state.userName} placement={"bottomRight"}>
                <span className='username-text'>{this.state.userName}</span>
            </Tooltip>
            <Popconfirm
                placement={"topLeft"}
                title={`确定要切换用户吗`}
                onConfirm={this.switchUser}
            >
                <Icon type="reload" className="reload-btn"/>
            </Popconfirm>
        </div>
    }
}

export default UserName;